import { simpleWeather } from '../services/Formurl';
import { message } from 'antd';

const dealPic = (wea) => {
  let pic
  if (wea === '晴') {
    pic = '/images/imgs/04天气/pic50.png';
  } else if (wea === '阴') {
    pic = '/images/imgs/04天气/pic60.png';
  } else if (wea === '多云'||wea === '阴转多云') {
    pic = '/images/imgs/04天气/pic61.png';
  } else if (wea === '小雨转阴') {
    pic = '/images/imgs/04天气/pic64.png';
  } else if (wea === '小雨转多云') {
    pic = '/images/imgs/04天气/pic64.png';
  } else if (wea === '多云转晴') {
    pic = '/images/imgs/04天气/多云转晴.png';
  } else if (wea === '小雨转晴') {
    pic = '/images/imgs/04天气/pic53.png';
  } else if (wea === '阴转小雨'||wea === '多云转小雨') {
    pic = '/images/imgs/04天气/pic64.png';
  } else if (wea === '晴转多云') {
    pic = '/images/imgs/04天气/pic52.png';
  }else if (wea === '多云转阴') {
    pic = '/images/imgs/04天气/pic52.png';
  }else if (wea === '阴转晴') {
    pic = '/images/imgs/04天气/pic52.png';
  }else if (wea === '小雨') {
    pic = '/images/imgs/04天气/pic62.png';
  }else if (wea === '晴转阴') {
    pic = '/images/imgs/04天气/pic51.png';
  }
  return pic;
};

export default {
  namespace: 'pagetestbtn',
  state: {
    mapData: 0,
    opend: '',
  },
  effects: {
    *getProjectMap({ payload }, { call, put }) {
      const response = yield call(simpleWeather);
      let mapData
      console.log(response.status)
      if (response.error_code === 0) {
        response.result.future.forEach((item, index) => {
          item.temperature = item.temperature.replace('/', '|');
          item.wetherPic = dealPic(item.weather);
        });
        const wea = response.result.realtime.info;
        response.result.realtime.pic = dealPic(wea)

        mapData = response.result;
        console.log(response);
      } else {
        mapData = {
          future: [{
            id: '1',
            temperature: "14|20℃",
            weather: "晴",
            wetherPic: "/images/imgs/04天气/pic50.png"
          }, {
            id: '2',
            temperature: "14|20℃",
            weather: "阴",
            wetherPic: "/images/imgs/04天气/pic60.png",
          }, {
            id: '3',
            temperature: "14|20℃",
            weather: "多云",
            wetherPic: "/images/imgs/04天气/pic61.png",
          }, {
            id: '4',
            temperature: "14|20℃",
            weather: "小雨转阴",
            wetherPic: "/images/imgs/04天气/pic64.png",
          }, {
            id: '5',
            temperature: "14|20℃",
            weather: "小雨转晴",
            wetherPic: "/images/imgs/04天气/pic53.png",
          }],
          realtime: {
            info: "晴",
            pic: "/images/imgs/04天气/pic50.png",
            temperature: "20",
          }
        }
       
        // return;
      }
      yield put({
        type: 'loadMapData',
        payload: mapData,
      });
    },
  },
  reducers: {
    loadMapData(state, { payload }) {
      return { ...state, weather: payload };
    },
    setOpened(state) {
      return {
        ...state,
        opend: true,
      }
    }
  },
};
